import {
  FlatList,
  Image,
  Keyboard,
  Platform,
  StatusBar,
  TouchableOpacity,
  TouchableWithoutFeedback,
  View,
} from 'react-native';
import HeaderCM from '../../../../components/Header/HeaderCM';
import styles from './styles';
import {
  IconArrowGrey,
  IconEmpty,
  IconHozDot,
  IconMail,
  IconPerson,
  IconPhone,
  IconRelative,
  IconTree,
  Iconhigh,
  Iconlow,
  Iconmedium,
} from '../../../../../assets/images';
import {ScreenHeight, SizeDP} from '../../../../../constants/Size';
import WebView from 'react-native-webview';
import {useCallback, useEffect, useRef, useState} from 'react';
import {SafeAreaView} from 'react-native-safe-area-context';
import {Color, NavigationName, String} from '../../../../../constants';
import DropdownCM, {IDataDropdown} from '../../../../components/DropDown';
import {Propity} from '../../../../../model/enum/common.enum';
import BottomSheetCM from '../../../../components/BottomSheet';
import {useFocusEffect, useNavigation} from '@react-navigation/native';
import {IModeler} from '../../../../../model/interface/modeler.interface';
import {isEmpty} from 'lodash';
import {useAppSelector} from '../../../../../hooks/useRedux';
import TextCM from '../../../../components/Text';
import ModalConfirmCM from '../../../../components/ModalConfirm';
import TextFieldCM from '../../../../components/TextField';
import LoadingCM from '../../../../components/Loading';
import {ITicket} from '../../../../../model/interface/service.interface';
import CardTicketRelative from '../CardTicketRelative';
import {useTranslation} from 'react-i18next';
import {Divider, IndexPath} from '@ui-kitten/components';
import {useTicketServices} from '../../../../../hooks/apiHooks/useTicketServices';
import {
  IAclProcess,
  IAclTicket,
  IImplementer,
  IPerson,
  IVariable,
} from './interface';
import {useReviewServices} from '../../../../../hooks/apiHooks/useReviewTicketServices';
import React from 'react';
import {TabName} from '../../../../../navigation/BottomTabERequest';
import {getConfig} from '../../../../../services/httpClient';
import Toast from 'react-native-toast-message';
import ModalFullScreen from '../../../../components/ModalFullScreen';
import DropdownListCM from '../../../../components/DropDownList';
import {OverflowMenuCM} from '../../../../components/OverflowMenu';
import ButtonCM from '../../../../components/Button';
const dataProrityDummy = [
  {
    id: 1,
    key: 'TK.high',
    value: Propity.high,
    icon: <Iconhigh width={SizeDP(12)} height={SizeDP(12)} />,
  },
  {
    id: 2,
    key: 'TK.medium',
    value: Propity.medium,
    icon: <Iconmedium width={SizeDP(12)} height={SizeDP(12)} />,
  },
  {
    id: 3,
    key: 'TK.low',
    value: Propity.low,
    icon: <Iconlow width={SizeDP(12)} height={SizeDP(12)} />,
  },
];

const dataTiket = [
  {
    id: 1,
    key: 'HM.information',
    value: 'information',
    icon: <IconPerson width={SizeDP(20)} height={SizeDP(20)} />,
  },
  {
    id: 2,
    key: 'HM.relative',
    value: 'relative',
    icon: <IconRelative width={SizeDP(20)} height={SizeDP(20)} />,
  },
];

const CreateTicketScreen = ({route}: any) => {
  const {t} = useTranslation();
  const webviewRef = useRef<WebView>(null);
  const navigation = useNavigation<any>();
  const action = route?.params?.action ?? 'create';

  const [dataPrority, setDataPrority] = useState<IDataDropdown>(
    dataProrityDummy[2],
  );
  const [dataModeler, setDataModeler] = useState<IModeler>();
  const [listAclProcess, setListAclProcess] = useState<IAclProcess[]>([]);
  const [listImplement, setListImplement] = useState<IImplementer[]>([]);
  const [listPersonRelateProcess, setListPersonRelateProcess] = useState<
    IPerson[]
  >([]);
  const [ticketStatusSelect, setTicketStatusSelect] = useState<
    'processing' | 'draft'
  >('processing');
  const [variableData, setVariableData] = useState<IVariable[]>([]);
  const [formVersion, setFormVersion] = useState<string>();
  const [ticketName, setTicketName] = useState<string>('');
  const [errorMessage, setErrorMessage] = useState<string>('');
  const [isVisible, setIsVisible] = useState<boolean>(false);
  const [isVisibleTicketRelative, setIsVisibleTicketRelative] =
    useState<boolean>(false);
  const [isVisibleCancel, setIsVisibleCancel] = useState<boolean>(false);
  const [isVisibleNoti, setIsVisibleNoti] = useState<boolean>(false);
  const [isVisibleInfo, setIsVisibleInfo] = useState<boolean>(false);
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [dataTicket, setDataTicket] = useState<any>();
  const [heightOfKeyboard, setHeightOfKeyboard] = useState(0);
  const ListServices = useTicketServices();
  const ReviewService = useReviewServices();

  const dataAccount = useAppSelector(state => state.account.dataAccount);
  const token = useAppSelector(state => state.common.accessToken);
  useFocusEffect(
    useCallback(() => {
      if (route?.params?.paramsSelectTicket?.length >= 0) {
        setIsVisibleTicketRelative(true);
      }
    }, [route?.params]),
  );

  useEffect(() => {
    const sub = Keyboard.addListener(
      Platform.OS === 'android' ? 'keyboardDidShow' : 'keyboardWillShow',
      e => {
        setHeightOfKeyboard(e.endCoordinates.height);
      },
    );
    const sub1 = Keyboard.addListener(
      Platform.OS === 'android' ? 'keyboardDidHide' : 'keyboardWillHide',
      e => {
        setHeightOfKeyboard(0);
      },
    );
    return () => {
      sub.remove();
      sub1.remove();
    };
  }, []);

  useEffect(() => {
    (async () => {
      try {
        setTicketName('');
        if (route?.params?.dataTicket?.modelerId) {
          await getDataModerler();
        }
        if (route?.params?.dataTicket?.id && action !== 'create') {
          const data = await getTicketById();
          if (data) {
            setDataTicket(data);
            setTicketName(data?.title);
            const priority = dataProrityDummy.find(
              p => p?.value === data.priority,
            );
            if (priority) {
              setDataPrority(priority);
            }
            const dataForm = data.formData?.find(f => !f.userTaskId);
            if (dataForm && dataForm.formData) {
              setFormVersion(dataForm.version);
            }

            // get ticket relative
            const dataTicketRelative = data.listRelativeTicket;
            if (dataTicketRelative.length > 0) {
              navigation.setParams({
                ...route?.params,
                paramsSelectTicket: dataTicketRelative,
              });
            }
          }
        }
      } catch (error) {}
    })();
  }, []);

  const getDataModerler = async () => {
    try {
      const values = await Promise.all([
        ListServices.getModeler(route?.params?.dataTicket?.modelerId, token),
        ListServices.getImplementByModelerId(
          route?.params?.dataTicket?.modelerId,
        ),
        ListServices.getAclProcessByModelerId(
          route?.params?.dataTicket?.modelerId,
        ),
      ]);
      const dataModeler = values[0] as IModeler;
      setDataModeler(dataModeler);
      setFormVersion(dataModeler.formVersion);
      const listAclProcess = values[2].data.content as IAclProcess[];
      const listImplement = values[1].data as IImplementer[];
      setListImplement(listImplement);
      setListAclProcess(listAclProcess);

      const listFollowId = dataModeler.followerId
        ? dataModeler.followerId.split(',').map(Number)
        : [];
      const listManagerId = dataModeler.managerId
        ? dataModeler.managerId.split(',').map(Number)
        : [];
      const listUserId = Array.from(
        new Set(
          listFollowId
            .concat(listManagerId)
            .filter(u => !listImplement.some(i => +i?.executorId === u)),
        ),
      );
      const listApi = [];
      if (dataModeler.documentId) {
        listApi.push(
          ListServices.getListDataVariableConfig(dataModeler.documentId),
        );
      }
      if (listUserId.length > 0) {
        listApi.push(
          ReviewService.findUserByOrgIn(0, 10000, '', null, listUserId),
        );
      }
      const valuesUser = await Promise.all(listApi);
      let variableData;
      let listPersonRelateProcess: IPerson[] = [];
      if (dataModeler.documentId && !listUserId.length) {
        variableData = valuesUser[0].variables.filter(
          (v: any) => v.apply && v.resourceId === dataModeler.formId,
        );
      } else if (!dataModeler.documentId && listUserId.length) {
        listPersonRelateProcess = listUserId.map(userId => {
          const per = (valuesUser[0].data as IPerson[]).find(
            u => u.resourceId === userId,
          );
          return per;
        }) as IPerson[];
      } else {
        variableData = valuesUser[0].variables.filter(
          (v: any) => v.apply && v.resourceId === dataModeler.formId,
        );
        listPersonRelateProcess = listUserId.map(userId => {
          const per = (valuesUser[1].data as IPerson[]).find(
            u => u.resourceId === userId,
          );
          return per;
        }) as IPerson[];
      }
      setVariableData(variableData);
      setListPersonRelateProcess(listPersonRelateProcess);
    } catch (error) {}
  };

  const onMessage = async (event: any) => {
    const res = JSON.parse(event.nativeEvent.data);
    if (res.result === 'e-from') {
      if (action !== 'create') {
        if (dataTicket) {
          const dataForm = dataTicket.formData?.find(f => !f.userTaskId);
          if (dataForm && dataForm.formData) {
            // set value to form
            const formData = JSON.stringify({
              type: 'postFormEdit',
              data: JSON.parse(dataForm.formData),
            });

            const scriptJSX = `window.postMessage('${formData
              .replace(/\\/g, '\\\\')
              .replace(/'/g, "\\'")}', '*');`;
            setTimeout(() => {
              webviewRef?.current?.injectJavaScript(scriptJSX);
            }, 800);
          }
        }
      }
    }

    if (res.result == 'result') {
      setIsLoading(true);
      const dataSelect = route?.params?.dataTicket;
      const listRelativeTicket = route?.params?.paramsSelectTicket
        ? route?.params?.paramsSelectTicket.map((item: ITicket) => {
            return {
              id: item.id,
            };
          })
        : [];

      if (dataSelect && dataModeler) {
        const aclTicketDTOS = getAclTicketDTOS(dataModeler);
        const dataBody = {
          aclTicketDTOS: aclTicketDTOS,
          followerId: dataModeler.followerId,
          formData: [
            {
              eformId: dataModeler.formId,
              formData: JSON.stringify(
                res?.data?.map((item: any) => {
                  return {
                    ...item,
                    dataType: convertTypeField(item),
                    typeForm: item.type,
                    value: item.value,
                  };
                }),
              ),
              ticketId: null,
              userTaskId: null,
              version: formVersion,
            },
          ],
          jsonXml: dataModeler.jsonXml,
          listRelativeTicket: listRelativeTicket,
          priority: dataPrority.value,
          ticketStatus: ticketStatusSelect,
          title: ticketName,
          workFlowId:
            action !== 'create' ? dataSelect.workFlowId : dataSelect.id,
          modelerId: route?.params?.dataTicket?.modelerId,
        };

        try {
          if (action === 'edit') {
            const body = {
              ...dataTicket,
              aclTicketDTOS: aclTicketDTOS,
              ticketStatus: ticketStatusSelect,
              title: ticketName,
              listRelativeTicket: listRelativeTicket,
              modelerId: route?.params?.dataTicket?.modelerId,
            };
            const dataTicketResp = await ListServices.updateTicket(body);
            if (dataTicketResp && ticketStatusSelect === 'draft') {
              navigation.navigate(TabName.MyTicket);
              Toast.show({
                type: 'success',
                text1: t('CM.noti'),
                text2: t('To bn nhp thnh cng'),
              });
              return;
            }
            if (dataTicketResp) {
              navigation.navigate(TabName.Home);
            }
          } else {
            const dataTicketResp = await ListServices.createTicket(
              dataBody,
              token,
            );
            if (dataTicketResp && ticketStatusSelect === 'draft') {
              navigation.navigate(TabName.MyTicket);
              Toast.show({
                type: 'success',
                text1: t('CM.noti'),
                text2: t('To bn nhp thnh cng'),
              });
              return;
            }

            if (dataModeler?.documentId) {
              const dataJson = res?.data.filter((d: any) =>
                variableData.some(v => v.fieldId == d.formId),
              );
              if (dataJson?.length > 0) {
                const dataField = dataJson.map((data: any) => {
                  const find = variableData.find(
                    v => v.fieldId === data.formId,
                  );
                  return {
                    ...data,
                    code: find?.code,
                  };
                });
                const dataAdd = [
                  {
                    dataJson: dataField.reduce((tot: any, curr: any) => {
                      tot = {...tot, [curr.code]: curr.value};
                      return tot;
                    }, {}),
                  },
                ];
                try {
                  const dataSaveEwriter =
                    await ListServices.saveDataVariableEwriter(
                      dataAdd,
                      dataModeler.documentId,
                    );
                  if (dataSaveEwriter) {
                    navigation.navigate(NavigationName.CreateTicketSuccess, {
                      ticketId: dataTicketResp?.id,
                    });
                  } else {
                    Toast.show({
                      type: 'error',
                      text1: t('CM.noti'),
                      text2: 'Lu thng tin bin vo ti liu Ewriter tht bi',
                    });
                  }
                  return;
                } catch (error) {}
              }
            }
            if (dataTicketResp) {
              navigation.navigate(NavigationName.CreateTicketSuccess, {
                ticketId: dataTicketResp?.id,
              });
            }
          }
        } catch (error) {
          console.log(error);
        } finally {
          setTicketName('');
          setIsLoading(false);
        }
      }
    }
  };

  const getAclTicketDTOS = (dataModeler: IModeler) => {
    let aclTicketDTOS = [];
    const listAclRelateTicket = listPersonRelateProcess.map(u => {
      const person = {
        id: u.resourceId,
        email: u.email,
      };
      const perm = ['v-d', 'v-i'];
      return {
        userTaskId: null,
        resourceName: ticketName,
        resourceType: 't',
        sharedType: 'p',
        personalStr: JSON.stringify(person),
        type: 'internal',
        perm: JSON.stringify(perm),
        custId: u.custId,
        orgIn: u.orgIn,
        createdBy: dataModeler.createdBy,
        typePerson: 'aclRelate',
      };
    });
    const listImplementAcl = listImplement.map(i => {
      const person = {
        id: +i?.executorId,
        email: i?.email,
      };
      const perm = ['v-d', 'v-i', 'e'];
      return {
        userTaskId: i.userTaskId,
        resourceName: ticketName,
        resourceType: 't',
        sharedType: i.executorId ? 'p' : null,
        personalStr: i.executorId ? JSON.stringify(person) : null,
        type: 'internal',
        perm: JSON.stringify(perm),
        custId: i.executorId ? i?.custId : null,
        orgIn: i.executorId ? i?.orgIn : null,
        createdBy: i.createdBy,
        typePerson: 'implement',
      };
    });
    const listSharedAcl: Partial<IAclTicket>[] = listAclProcess.map(acl => {
      return {
        userTaskId: acl.userTaskId,
        resourceName: ticketName,
        resourceType: 't',
        sharedType: acl.sharedType,
        personalStr: JSON.stringify(acl.personal),
        type: acl.type,
        perm: acl.perm,
        custId: acl.custId,
        orgIn: acl.orgIn,
        createdBy: acl.createdBy,
        typePerson: 'shareAcl',
      };
    });
    aclTicketDTOS = [
      ...listAclRelateTicket,
      ...listImplementAcl,
      ...listSharedAcl,
    ];
    return aclTicketDTOS;
  };

  const getTicketById = async () => {
    try {
      const data = (await ListServices.getTicketById(
        route?.params?.dataTicket?.id,
        token,
      )) as ITicket;
      return data;
    } catch (error) {
      console.log(error);
    }
  };

  const convertTypeField = (item: any) => {
    if (
      [
        'textField',
        'textArea',
        'password',
        'email',
        'phone_number',
        'radio_button',
        'upload_file',
        'upload_img',
        'hyperlink',
        'datepicker',
        'signature',
      ].includes(item.type)
    ) {
      return 'String';
    } else if (item.type === 'number') {
      return 'Number';
    }
    //  else if (item.type === 'datepicker') {
    //   return 'Date';
    // }
    else if (['check_box', 'dropdown'].includes(item.type)) {
      if (
        (item.type === 'dropdown' && item.validate.mutilChoose) ||
        item.type === 'check_box'
      ) {
        return 'List';
      }
      return 'String';
    }
    return null;
  };

  const handelSelectInfo = (item: IDataDropdown) => {
    if (item.value === 'information') {
      setIsVisible(true);
    } else if (item.value === 'relative') {
      setIsVisibleTicketRelative(true);
    }
  };

  const handleCancel = (type: 'noti' | 'cancel' | 'info') => {
    switch (type) {
      case 'noti':
        setIsVisibleNoti(false);
        setIsVisibleInfo(true);
        break;
      case 'cancel':
        setIsVisibleCancel(false);
        break;
      case 'info':
        setIsVisibleInfo(false);
        setTicketName('');
        setErrorMessage('');
        break;
    }
  };

  const handleConfirmData = (type: 'processing' | 'draft') => {
    setTicketStatusSelect(type);
    if (isEmpty(ticketName) || action === 'edit' || action === 'multi') {
      setIsVisibleInfo(true);
    } else {
      handleConfirmNoti();
    }
  };

  const handleConfirmCancel = () => {
    setIsVisibleCancel(false);
    navigation.goBack();
  };

  const handleConfirmNoti = () => {
    const data = JSON.stringify({
      result: 'submit',
    });
    const scriptJSX = `window.postMessage('${data}', '*');`;
    webviewRef?.current?.injectJavaScript(scriptJSX);
    setIsVisibleNoti(false);
  };

  const handleConfirmInfo = () => {
    if (isEmpty(ticketName.trim())) {
      setErrorMessage(t('HM.notEmpty'));
      return;
    } else {
      handleConfirmNoti();
    }
    setIsVisibleInfo(false);
    setIsVisibleNoti(true);
    setErrorMessage('');
  };

  const handleAddTicket = () => {
    setIsVisibleTicketRelative(false);
    navigation.navigate(NavigationName.AddTicketRelativeS, {
      paramsSelectTicket: route?.params?.paramsSelectTicket
        ? route?.params?.paramsSelectTicket
        : [],
      onGoBack: (paramsSelectTicket: ITicket[]) => {
        navigation.setParams({paramsSelectTicket});
      },
    });
  };

  const renderRightHeader = () => {
    return (
      <View style={styles.ctnRenderRightHeader}>
        <DropdownCM
          listDropdown={dataProrityDummy}
          onChangeItem={setDataPrority}
          dropDownStyle={{right: SizeDP(50)}}
          renderShowData={
            <View style={styles.renderShow}>
              {dataPrority.icon}
              <IconArrowGrey
                fill={'#fff'}
                width={SizeDP(12)}
                height={SizeDP(12)}
              />
            </View>
          }
        />
        <View>
          <DropdownCM
            listDropdown={dataTiket}
            onChangeItem={item => handelSelectInfo(item)}
            containerStyle={{width: SizeDP(12)}}
          />
        </View>
      </View>
    );
  };

  const renderPersonTicket = () => {
    return (
      <>
        {!isEmpty(dataAccount) && (
          <View style={styles.ctnTicketPerson}>
            <View style={styles.ctnUser}>
              <Image
                width={SizeDP(50)}
                height={SizeDP(50)}
                borderRadius={SizeDP(25)}
                source={{uri: dataAccount.imageUrl || String.DEFAULT_IMAGE}}
              />
              <TextCM>
                {'' + dataAccount.firstName + ' ' + dataAccount.lastName}
              </TextCM>
            </View>
            <View style={styles.ctnSub}>
              <IconMail width={SizeDP(24)} height={SizeDP(24)} />
              <TextCM>{'' + dataAccount.email}</TextCM>
            </View>
            <View style={styles.ctnSub}>
              <IconTree width={SizeDP(24)} height={SizeDP(24)} />
              <TextCM>{'' + dataAccount.acNameOrg}</TextCM>
            </View>
            <View style={styles.ctnSub}>
              <IconPhone width={SizeDP(24)} height={SizeDP(24)} />
              <TextCM>{'' + dataAccount.phone}</TextCM>
            </View>
          </View>
        )}
      </>
    );
  };

  const renderTicketRelative = () => {
    return (
      <>
        <HeaderCM
          style={{
            backgroundColor: '#fff',
          }}
          contentStyle={{
            backgroundColor: '#fff',
          }}
          titleStyle={{
            color: '#000810',
          }}
          fillIconBackLeft="#000810"
          title={t('HM.relatedTicket')}
          onPressIconLeft={() => setIsVisibleTicketRelative(false)}
          renderContentRight={() => (
            <TouchableOpacity onPress={handleAddTicket}>
              <TextCM style={styles.txtRight}>{t('CM.add')}</TextCM>
            </TouchableOpacity>
          )}
        />
        <Divider />
        {isEmpty(route?.params?.paramsSelectTicket) ? (
          <View style={styles.ctnArea}>
            <IconEmpty width={SizeDP(218)} height={SizeDP(149)} />
            <TextCM style={styles.textTitle}>
              {t('HM.relativeTicketEmpty')}
            </TextCM>
            <TextCM style={styles.textDes}>{t('HM.desRelativeTicket')}</TextCM>
          </View>
        ) : (
          <FlatList
            style={{paddingHorizontal: 16}}
            data={route?.params?.paramsSelectTicket}
            showsHorizontalScrollIndicator={false}
            showsVerticalScrollIndicator={false}
            renderItem={({item}: {item: ITicket}) => (
              <CardTicketRelative
                dataCard={item}
                account={dataAccount}
                select={route?.params?.paramsSelectTicket}
                onSelect={paramsSelectTicket =>
                  navigation.setParams({paramsSelectTicket})
                }
              />
            )}
          />
        )}
      </>
    );
  };

  const getHeader = () => {
    switch (action) {
      case 'create':
        return t('HM.addSearch');
      case 'multi':
        return t('HM.muiTicket');
      case 'edit':
        return t('HM.editTicket');
      default:
        return '';
    }
  };

  return (
    <>
      <TouchableWithoutFeedback onPress={() => Keyboard.dismiss()}>
        <>
          <StatusBar
            translucent
            barStyle={'dark-content'}
            backgroundColor={Color.Transparent}
          />
          <HeaderCM
            style={styles.ctnHeader}
            contentStyle={styles.ctnHeader}
            titleStyle={{
              color: Color.TextPrimary,
            }}
            title={getHeader()}
            fillIconBackLeft=""
            renderContentRight={renderRightHeader}
          />
          <Divider />
          <View
            style={[
              {flex: 1, backgroundColor: Color.BgBlur},
              Platform.OS === 'android' && {
                paddingBottom:
                  heightOfKeyboard > 0 ? heightOfKeyboard - 100 : 0,
              },
            ]}>
            {!isEmpty(formVersion) && (
              <WebView
                nestedScrollEnabled={true}
                showsHorizontalScrollIndicator={false}
                showsVerticalScrollIndicator={false}
                startInLoadingState
                injectedJavaScript={String.SCRIPT_SCALE_WEBVIEW}
                allowsInlineMediaPlayback={true}
                mediaPlaybackRequiresUserAction={false}
                ref={webviewRef}
                onMessage={onMessage}
                source={{uri: getConfig().BASE_URL_FORM + formVersion}}
                webviewDebuggingEnabled
              />
            )}
          </View>
          <Divider />
          <SafeAreaView
            edges={['bottom', 'left', 'right']}
            style={styles.ctnBtn}>
            <ButtonCM
              style={[{flex: 0.3}, styles.btnCancel]}
              label={t('CM.labelCancel')}
              styleLabel={[styles.textCancel]}
              onPress={() => setIsVisibleCancel(true)}
            />

            <ButtonCM
              style={[{flex: 1}, styles.btnLeft]}
              label={t('Lu nhp')}
              styleLabel={[styles.labelStyleLeft]}
              onPress={() => handleConfirmData('draft')}
            />

            <ButtonCM
              style={{flex: 1}}
              label={t('HM.sendServices')}
              onPress={() => handleConfirmData('processing')}
            />
          </SafeAreaView>

          <BottomSheetCM
            isVisible={isVisible}
            onClose={() => setIsVisible(false)}
            title={t('HM.infoPerson')}
            renderContent={renderPersonTicket()}
          />

          <ModalFullScreen
            isVisible={isVisibleTicketRelative}
            children={renderTicketRelative()}
          />

          <ModalConfirmCM
            isVisible={isVisibleCancel}
            onClose={() => handleCancel('cancel')}
            title={t('HM.titleCancel')}
            content={t('HM.contentCancel')}
            type="cancel"
            labelCancel={t('HM.cancel')}
            labelConfirm={t('CM.labelCancel')}
            styleBtnRight={{backgroundColor: Color.RedE14337}}
            onConfirm={handleConfirmCancel}
          />

          <ModalConfirmCM
            isVisible={isVisibleInfo}
            onClose={() => handleCancel('info')}
            title={t('HM.serviceName')}
            content={t('HM.contentFill')}
            type="info"
            labelCancel={t('CM.labelCancel')}
            labelConfirm={t('HM.sendServices')}
            styleBtnRight={{backgroundColor: Color.BgPrimary}}
            onConfirm={handleConfirmInfo}
            renderContent={() => (
              <View style={{width: '100%', marginVertical: SizeDP(6)}}>
                <TextFieldCM
                  label={t('HM.enterName')}
                  value={ticketName}
                  onChangeText={setTicketName}
                  errorMessage={errorMessage}
                />
              </View>
            )}
          />

          <LoadingCM isVisible={isLoading} />
        </>
      </TouchableWithoutFeedback>
    </>
  );
};

export default CreateTicketScreen;
