import {Fragment, useEffect, useState} from 'react';
import {HeaderField, IFieldNotionEcontract} from '../interface';
import {useAppSelector} from '../../../../hooks/useRedux';
import {useReviewEcontractServices} from '../../../../hooks/apiHooks/useReviewEcontractServices';
import {Pressable, View} from 'react-native';
import InputCM from '../../review-eid/components/InputCM';
import {CheckBox, Icon, IndexPath, List, Spinner} from '@ui-kitten/components';
import TextCM from '../../../components/Text';
import styles from './styles';
import InputDate from '../components/InputDate';
import DropdownListCM from '../../../components/DropDownList';
import {headerFieldsColla, listRemind} from './helper';
import TextFieldCM from '../../../components/TextField';
import PartyScreen from './components/party';
import {EnvRemind, IDetailFile, IFieldIdFromColla, IParty} from './interface';
import {useTranslation} from 'react-i18next';
import ListItemField from './components/ListItemDoc';

// for DT flow only to check formdata to create econtract envelope
const dtFlowId = [
  // prod env

  // ----------------------------------
  // demo env
  '000010eViX8cWwWn5reR',
  '000010Nmi1KfN5azzXD4',
  '000010ayipEI5eN61LrV',
  '0000101JigKI6w6GvQ5G',
  // ----------------------------------

  // dev env
];

type Props = {
  dataEcontractFlow: any;
  error: {[key: string]: string};
  setError: any;
  setTemplateStructure: any;
  setMergeFileList: any;
  setEWriterDocInfoWriter: any;
  setEnvRemind: (data: any) => void;
  envRemind: EnvRemind;
  setIsDTFlow: (isDTFlow: boolean) => void;
  dataPartyForm: IParty[];
  setDataPartyForm: (data: IParty[]) => void;
  listFormEnvData: HeaderField[];
  setListFormEnvData: (data: HeaderField[]) => void;
  setListCaOtpRecipientForm: any;
  setIsErrorView: (data: {isError: boolean; errorViewMessage: string}) => void;
  isErrorView: {isError: boolean; errorViewMessage: string};
  setDocumentList: (data: IFieldIdFromColla[]) => void;
};

const ReviewEcontract = ({
  dataEcontractFlow,
  error,
  setError,
  setTemplateStructure,
  setMergeFileList,
  setEWriterDocInfoWriter,
  setEnvRemind,
  envRemind,
  setIsDTFlow,
  dataPartyForm,
  setDataPartyForm,
  listFormEnvData,
  setListFormEnvData,
  setListCaOtpRecipientForm,
  setIsErrorView,
  isErrorView,
  setDocumentList,
}: Props) => {
  const {t} = useTranslation();
  const [isShowInfo, setIsShowInfo] = useState<boolean>(true);
  const [isLoading, setIsLoading] = useState<boolean>(true);
  const [isShowInfoSign, setIsShowInfoSign] = useState<boolean>(true);
  const [recipientDetail, setRecipientDetail] = useState();
  const [listFieldIdDocFromColla, setFieldIdDocFromColla] = useState<
    IDetailFile[]
  >([]);
  const {dataTicketRedux} = useAppSelector(state => state.ticket);
  const [typeSelectNoti, setTypeSelectNoti] = useState<IndexPath>(
    new IndexPath(0),
  );
  const {accessToken} = useAppSelector(state => state.common);

  const TicketReviewEcontractServices = useReviewEcontractServices();

  useEffect(() => {
    handleInitData();
  }, []);

  useEffect(() => {
    setEnvRemind((prev: EnvRemind) => ({
      ...prev,
      remindFrequency: listRemind[typeSelectNoti.row].value,
    }));
  }, [typeSelectNoti]);

  const handleInitData = async () => {
    try {
      setIsLoading(true);
      if (dataEcontractFlow?.userTaskId) {
        // if (dataTicketRedux.id) {
        //   await getRecipientDetail();
        // }

        if (dataEcontractFlow.documentResource === 'ewriter') {
          await handleDocumentEWriter();
        } else if (dataEcontractFlow.documentResource === 'ecollabo') {
          await handleDocumentECollabor();
        }
      }
    } catch (error) {
    } finally {
      setIsLoading(false);
    }
  };

  const handleDocumentEWriter = async () => {
    const eWriterDocInfoRequest = await getEWriterDocByUserTaskIdAndTicketId();

    if (eWriterDocInfoRequest) {
      const modelerId = dataTicketRedux.modelerId;
      if (modelerId && dtFlowId.includes(modelerId)) {
        // for DT flow
        const formList = await TicketReviewEcontractServices.getTicketForms(
          dataTicketRedux.id,
        );
        const mainForm = (formList as any[]).find(form => !form.userTaskId);
        if (mainForm && mainForm.formData) {
          const formData = JSON.parse(mainForm.formData) as Array<any>;
          const contractField = formData.find(
            field => field.label === 'contract',
          );
          const categoryField = formData.find(
            field => field.label === 'category',
          );
          if (contractField && categoryField) {
            await handleDocumentEWriterForDT(
              eWriterDocInfoRequest,
              contractField.value,
              categoryField.value,
            );
          }
        }
      } else {
        await handleDefaultDocumentEWriter(eWriterDocInfoRequest);
      }
    }
  };

  const handleDocumentEWriterForDT = async (
    eWriterDocInfoRequest: any[],
    _fileName: string,
    _category: 'Maintenance' | 'Troubleshoot',
  ) => {
    setIsDTFlow(true);
    if (eWriterDocInfoRequest.length > 1) {
      try {
        const values = await Promise.all(
          eWriterDocInfoRequest.map(doc => {
            const params = {
              Id: doc.eWriterId,
            };
            return TicketReviewEcontractServices.getEcontractDocInfo(params);
          }),
        );
        if (values) {
          const documentList = values;
          if (documentList) {
            const fileName = handleFileNameWhitespace(_fileName, _category);
            const eWriterFlowDocument = documentList.find(
              doc => (doc.name && doc.name.split('.')[0]) === fileName,
            );
            if (
              eWriterFlowDocument &&
              eWriterFlowDocument.templateEcontractId
            ) {
              await initEContractDatas(eWriterFlowDocument);
            }
          }
        }
      } catch (error) {}
    }
  };

  const handleFileNameWhitespace = (
    fileName: string,
    category: 'Maintenance' | 'Troubleshoot',
  ) => {
    let contract = fileName.split(' ').join('');
    return category === 'Troubleshoot'
      ? `KPSC_${contract}`
      : `BDK_${contract}`;
  };

  const handleDefaultDocumentEWriter = async (
    eWriterDocInfoRequest: IFieldNotionEcontract[],
  ) => {
    if (eWriterDocInfoRequest.length > 1) {
      await getEcontractDocInfo(eWriterDocInfoRequest);
    }
    const eWriterDocInfoWriter: any =
      await TicketReviewEcontractServices.getEcontractDocInfo({
        Id: eWriterDocInfoRequest[0]?.eWriterId,
      });

    if (eWriterDocInfoWriter && eWriterDocInfoWriter.templateEcontractId) {
      await initEContractDatas(eWriterDocInfoWriter);
    }
  };

  const initEContractDatas = async (eWriterDocInfoWriter: any) => {
    setEWriterDocInfoWriter(eWriterDocInfoWriter);
    const params = {
      templateid: eWriterDocInfoWriter.templateEcontractId,
    };

    const templateStructureResponse =
      await TicketReviewEcontractServices.getStructureOfData(
        params,
        accessToken,
      );

    if (templateStructureResponse) {
      setTemplateStructure(templateStructureResponse);
      const templateStructureDatas =
        templateStructureResponse?.lotData?.datas[0];
      setListFormEnvData(
        templateStructureDatas.filter(
          (data: HeaderField) =>
            data.id !== 'envSubmittedFrom' && data.type === 'header_fields',
        ),
      );
      setIsErrorView({
        isError: false,
        errorViewMessage: '',
      });

      if (
        templateStructureDatas &&
        templateStructureDatas.filter(
          (data: HeaderField) => data.type !== 'header_fields',
        ).length > 0
      ) {
        createPartyForm(templateStructureResponse);
      }
    }
  };

  const createPartyForm = (templateStructureResponse: any) => {
    const newDataPartyForm: any[] = [];
    const templateStructureDatas = templateStructureResponse?.lotData?.datas[0];
    const partyArr = templateStructureDatas.filter(
      (data: HeaderField) => data.type === 'p',
    );

    newDataPartyForm.push(
      ...partyArr.map((party: HeaderField) => ({
        id: party.id,
        recipient: {
          reviewer: [],
          signer: [],
          stamper: [],
        },
      })),
    );
    newDataPartyForm.map(party => {
      const recipientInParty = templateStructureDatas.filter(
        (item: HeaderField) => item.type === 'r' && item.id.includes(party.id),
      );
      recipientInParty.forEach((recipient: any) => {
        createRecipientCaOTPFormGroup(
          templateStructureResponse.template,
          recipient.id,
        );
        const role = getRecipientRole(
          templateStructureResponse.template,
          recipient.id,
        );
        newDataPartyForm
          ?.find(partyX => partyX.id === party.id)
          ?.recipient?.[role].push(
            createRecipientFormGroup(recipient.id, templateStructureDatas),
          );
      });
    });
    setDataPartyForm(newDataPartyForm);
  };

  const createRecipientCaOTPFormGroup = (
    templateData: any,
    recipientId: string,
  ) => {
    templateData.parties.forEach((party: any) => {
      party.recipients.forEach((recipient: any) => {
        if (
          recipient.id === recipientId &&
          recipient.signTypes &&
          recipient.signTypes.find((type: string) => type === 'sign_fca.otp')
        ) {
          setListCaOtpRecipientForm((prev: any) => ({
            ...prev,
            [recipientId]: {
              recipientId: recipientId,
              email: recipient.contact.email,
              telephoneNumber: recipient.contact.phone,
              contactId: recipient.contact.id,
              personalName: recipient.contact.name,
              location: null,
              stateOrProvince: null,
              country: null,
              personalID: null,
              passportID: null,
              type: 'fca_pre',
              photoIDCard: null,
              photoIDCardContentType: null,
              photoFrontSideIDCard: null,
              photoFrontSideIDCardContentType: null,
              photoBackSideIDCard: '',
              photoBackSideIDCardContentType: null,
              statusCode: null,
              resourceType: null,
              provideAddress: null,
              provideDate: null,
              commune: null,
              address: null,
              refId: null,
            },
          }));
        }
      });
    });
  };

  const createRecipientFormGroup = (
    recipientId: string,
    templateStructureDatas: any,
  ) => {
    const recipientInfos = templateStructureDatas.filter(
      (item: HeaderField) => item.id === recipientId,
    );
    const r = recipientInfos.find(
      (recipient: HeaderField) => recipient.type === 'r',
    );
    const m = recipientInfos.find(
      (recipient: HeaderField) => recipient.type === 'm',
    );
    const p = recipientInfos.find(
      (recipient: HeaderField) => recipient.type === 'phone',
    );
    const ct = recipientInfos.find(
      (recipient: HeaderField) => recipient.type === 'contact_recipient',
    );
    return {
      id: recipientId,
      name: r ? r.value : null,
      phone: p ? p.value : null,
      email: m ? m.value : null,
      contactRecipient: ct ? ct.value : null,
      notifyType: ['email'],
    };
  };

  const getRecipientRole = (templateData: any, recipientId: string) => {
    let role = '';
    if (templateData) {
      templateData.parties.forEach((party: any) => {
        party.recipients.forEach((recipient: any) => {
          if (recipient.id === recipientId) {
            role = recipient.role;
          }
        });
      });
    }
    return role;
  };

  const getEcontractDocInfo = async (
    eWriterDocInfoRequest: IFieldNotionEcontract[],
  ) => {
    try {
      Promise.all(
        eWriterDocInfoRequest.map(doc => {
          const params = {
            Id: doc.eWriterId,
          };
          return TicketReviewEcontractServices.getEcontractDocInfo(params);
        }),
      ).then(values => {
        const newMergeFileList = values.map((value, index) => ({
          fileId: value.id,
          fileName: value.name,
          order: index + 1,
        }));
        setMergeFileList(newMergeFileList);
      });
    } catch (error) {}
  };

  const getEWriterDocByUserTaskIdAndTicketId = async () => {
    try {
      const data =
        await TicketReviewEcontractServices.getEWriterDocByUserTaskIdAndTicketId(
          dataEcontractFlow?.userTaskId,
          dataTicketRedux.id,
        );
      return data;
    } catch (error) {}
  };

  const handleDocumentECollabor = async () => {
    await getEcontractListColaboDoc();
    setListFormEnvData(
      headerFieldsColla.filter(
        (data: HeaderField) => data.id !== 'envSubmittedFrom',
      ),
    );
  };

  const getEcontractListColaboDoc = async () => {
    const dataListDoc = (await TicketReviewEcontractServices.getListCollaboDocs(
      dataTicketRedux.id,
      dataEcontractFlow?.userTaskId,
    )) as IFieldIdFromColla[];

    if (dataListDoc) {
      setDocumentList(dataListDoc);
      const valueListDetail = await Promise.all(
        dataListDoc.map(data =>
          TicketReviewEcontractServices.getDetailCollabora(data.fileId),
        ),
      );
      setFieldIdDocFromColla(valueListDetail);
    }
  };

  const getRecipientDetail = async () => {
    try {
      const data = await TicketReviewEcontractServices.getRecipientDetail(
        dataEcontractFlow?.userTaskId,
        dataTicketRedux.id,
      );
      if (data) {
        setRecipientDetail(data);
      }
    } catch (error) {}
  };
  const handleChangeValue = (type: string, isRequired: boolean, value: any) => {
    setListFormEnvData(
      listFormEnvData.map(data =>
        data.id !== type
          ? data
          : {
              ...data,
              value: value,
            },
      ),
    );

    if (isRequired) {
      if (!value.trim()) {
        setError((prevErr: any) => ({
          ...prevErr,
          [type]: 'CM.empty',
        }));
      } else {
        setError((prevErr: any) => {
          const updatedErr = {...prevErr};
          delete updatedErr[type];
          return updatedErr;
        });
      }
    }
  };

  const handleChangeRemind = (type: string, value: any) => {
    setEnvRemind((prev: EnvRemind) => ({
      ...prev,
      [type]: value,
    }));

    if (type === 'isRemindEnv') {
      if (value === false) {
        setError((prevErr: any) => {
          const updatedErr = {...prevErr};
          delete updatedErr['overdueDate'];
          delete updatedErr['remindStartDate'];
          delete updatedErr['remindFrequency'];
          delete updatedErr['remindContent'];
          return updatedErr;
        });
        return;
      }
    }

    if (type === 'remindContent') return;
    if (!value) {
      setError((prevErr: any) => ({
        ...prevErr,
        [type]: 'CM.empty',
      }));
    } else {
      setError((prevErr: any) => {
        const updatedErr = {...prevErr};
        delete updatedErr[type];
        return updatedErr;
      });
    }
  };

  if (isLoading) {
    return (
      <View style={{flex: 1, justifyContent: 'center', alignItems: 'center'}}>
        <Spinner size="large" />
      </View>
    );
  }

  return (
    <View style={{backgroundColor: '#fff'}}>
      {!isErrorView.isError ? (
        <>
          <View>
            <Pressable
              style={[styles.ctnHeader]}
              onPress={() => setIsShowInfo(!isShowInfo)}>
              <Icon
                fill={'#6F2CED'}
                width={16}
                height={16}
                name={isShowInfo ? 'arrow-circle-down' : 'arrow-circle-right'}
              />
              <TextCM style={styles.textTitle}>
                {t('RV.econtract.infoContract')}{' '}
                <TextCM style={styles.redDot}>*</TextCM>
              </TextCM>
            </Pressable>
          </View>
          <View style={{paddingHorizontal: 10}}>
            {listFormEnvData &&
              isShowInfo &&
              listFormEnvData.map((data, index) => {
                return (
                  <>
                    {data.dataType !== 'Date' && (
                      <Fragment key={index}>
                        <InputCM
                          label={data.name}
                          value={data.value}
                          onChangeText={text =>
                            handleChangeValue(data.id, data.required, text)
                          }
                          isRequired={data.required}
                          captionText={t(error[data.id])}
                          status={error[data.id] ? 'danger' : 'basic'}
                          placeholder={'Nhp ' + data.name.toLowerCase()}
                        />
                      </Fragment>
                    )}
                    {data.dataType === 'Date' && (
                      <Fragment key={index}>
                        <InputDate
                          isRequired={data.required}
                          label={data.name}
                          value={data.value}
                          captionText={t(error[data.id])}
                          status={error[data.id] ? 'danger' : 'basic'}
                          onChangeDate={text =>
                            handleChangeValue(data.id, data.required, text)
                          }
                        />
                      </Fragment>
                    )}
                  </>
                );
              })}
            {isShowInfo && (
              <>
                <CheckBox
                  style={{paddingVertical: 8}}
                  checked={envRemind['isRemindEnv']}
                  onChange={nextChecked =>
                    handleChangeRemind('isRemindEnv', nextChecked)
                  }
                  children={props => (
                    <TextCM {...props}>{t('RV.econtract.remind')}</TextCM>
                  )}
                />
                {envRemind['isRemindEnv'] && (
                  <View style={{rowGap: 4}}>
                    <InputDate
                      isRequired={true}
                      label={t('RV.econtract.overdueDate')}
                      value={envRemind['overdueDate']}
                      captionText={t(error['overdueDate'])}
                      status={error['overdueDate'] ? 'danger' : 'basic'}
                      onChangeDate={date =>
                        handleChangeRemind('overdueDate', date)
                      }
                    />
                    <InputDate
                      isRequired={true}
                      label={t('RV.econtract.remindStartDate')}
                      value={envRemind['remindStartDate']}
                      captionText={t(error['remindStartDate'])}
                      status={error['remindStartDate'] ? 'danger' : 'basic'}
                      onChangeDate={date =>
                        handleChangeRemind('remindStartDate', date)
                      }
                    />
                    <DropdownListCM
                      onSelect={setTypeSelectNoti}
                      valueSelect={typeSelectNoti}
                      placeholder={t('RV.econtract.remindFrequency')}
                      label={t('RV.econtract.notiType')}
                      isRequired={true}
                      size="medium"
                      listData={listRemind}
                      caption={t(error[`remindFrequency`])}
                      status={error[`remindFrequency`] ? 'danger' : 'primary'}
                    />
                    <View style={{paddingVertical: 6, rowGap: 4}}>
                      <TextCM style={styles.textRemind}>
                        {t('CM.remind')}
                      </TextCM>
                      <TextFieldCM
                        isRequired={true}
                        label={t('CM.fillRemind')}
                        value={envRemind['remindContent']}
                        onChangeText={text =>
                          handleChangeRemind('remindContent', text)
                        }
                        multiline
                        maxLength={400}
                      />
                    </View>
                  </View>
                )}
              </>
            )}
          </View>
          <View>
            <Pressable
              style={[styles.ctnHeader]}
              onPress={() => setIsShowInfoSign(!isShowInfoSign)}>
              <Icon
                fill={'#6F2CED'}
                width={16}
                height={16}
                name={
                  isShowInfoSign ? 'arrow-circle-down' : 'arrow-circle-right'
                }
              />
              {dataEcontractFlow.documentResource === 'ewriter' ? (
                <TextCM style={styles.textTitle}>
                  {t('RV.econtract.listEwriter')}{' '}
                  <TextCM style={styles.redDot}>*</TextCM>
                </TextCM>
              ) : (
                <TextCM style={styles.textTitle}>
                  {t('RV.econtract.listEcollabo')}
                </TextCM>
              )}
            </Pressable>
          </View>

          {isShowInfoSign && (
            <>
              {dataEcontractFlow.documentResource === 'ewriter' ? (
                <>
                  {dataPartyForm &&
                    dataPartyForm.map((party, index) => (
                      <PartyScreen
                        error={error}
                        setError={setError}
                        party={party}
                        index={index + 1}
                      />
                    ))}
                </>
              ) : (
                <View
                  style={{
                    padding: 10,
                  }}>
                  <TextCM>{t('RV.econtract.listEcontract')}</TextCM>

                  {listFieldIdDocFromColla && (
                    <List
                      scrollEnabled={false}
                      data={listFieldIdDocFromColla}
                      renderItem={({item}) => (
                        <ListItemField typeReview="eContract" data={item} />
                      )}
                    />
                  )}
                </View>
              )}
            </>
          )}
        </>
      ) : (
        <TextCM style={styles.textError}>
          {t(isErrorView.errorViewMessage)}
        </TextCM>
      )}
    </View>
  );
};

export default ReviewEcontract;
