import {
  BottomTabBarProps,
  createBottomTabNavigator,
} from '@react-navigation/bottom-tabs';
import {SafeAreaView} from 'react-native-safe-area-context';
import styles from './styles';
import {TouchableOpacity, View} from 'react-native';
import {SizeDP} from '../../constants/Size';
import {
  IconFollowTicket,
  IconFollowTicketSelect,
  IconHome,
  IconHomeSelect,
  IconMyTicket,
  IconMyTicketSelect,
  IconNoti,
  IconNotiSelect,
} from '../../assets/images';
import i18n from '../../i18n';
import Home from '../../app/screens/home';
import TextCM from '../../app/components/Text';
import MyTicket from '../../app/screens/my-ticket';
import MyTicketFollow from '../../app/screens/ticket-follow';
import NotificationScreen from '../../app/screens/notification';
import {useTranslation} from 'react-i18next';
import {useEffect, useState} from 'react';
import {useAppDispatch} from '../../hooks/useRedux';
import {commonActions} from '../../redux/slices';
import {useNotificationServices} from '../../hooks/apiHooks/useNotificationServices';
export const TabName = {
  Home: 'Home',
  MyTicket: 'MyTicket',
  FollowTicket: 'FollowTicket',
  Notification: 'Notification',
};

const Tabs = createBottomTabNavigator();

const MyTabBar = ({state, navigation, t, totalRecoders}: any) => {
  return (
    <SafeAreaView
      edges={['bottom', 'left', 'right']}
      style={styles.ctnSafeTabBottom}>
      <View style={styles.ctnTabBottom}>
        {state.routes.map((route: any, index: number) => {
          let label = '';
          const isFocused = state.index === index;
          function getIcon() {
            switch (route.name) {
              case TabName.Home:
                label = t('CM.Home');
                return (
                  <>
                    {isFocused ? (
                      <IconHomeSelect width={SizeDP(23)} height={SizeDP(20)} />
                    ) : (
                      <IconHome width={SizeDP(23)} height={SizeDP(20)} />
                    )}
                  </>
                );
              case TabName.MyTicket:
                label = t('CM.MyTicket');
                return (
                  <>
                    {isFocused ? (
                      <IconMyTicketSelect
                        width={SizeDP(23)}
                        height={SizeDP(20)}
                      />
                    ) : (
                      <IconMyTicket width={SizeDP(23)} height={SizeDP(20)} />
                    )}
                  </>
                );
              case TabName.FollowTicket:
                label = t('CM.FollowTicket');
                return (
                  <>
                    {isFocused ? (
                      <IconFollowTicketSelect
                        width={SizeDP(23)}
                        height={SizeDP(20)}
                      />
                    ) : (
                      <IconFollowTicket
                        width={SizeDP(23)}
                        height={SizeDP(20)}
                      />
                    )}
                  </>
                );
              case TabName.Notification:
                label = t('CM.Notification');
                return (
                  <View style={{position: 'relative'}}>
                    {totalRecoders > 0 && (
                      <View
                        style={{
                          position: 'absolute',
                          backgroundColor: '#e14337',
                          borderRadius: 100,
                          paddingHorizontal: 4,
                          paddingVertical: 2,
                          top: -5,
                          right: totalRecoders > 100 ? -20 : -12,
                        }}>
                        <TextCM
                          style={{
                            fontSize: 10,
                            color: '#fff',
                          }}>
                          {totalRecoders < 100 ? totalRecoders : '99+'}
                        </TextCM>
                      </View>
                    )}
                    {isFocused ? (
                      <IconNotiSelect width={SizeDP(23)} height={SizeDP(20)} />
                    ) : (
                      <IconNoti width={SizeDP(23)} height={SizeDP(20)} />
                    )}
                  </View>
                );

              default:
                break;
            }
          }

          const onPress = () => {
            const event = navigation.emit({
              type: 'tabPress',
              target: route.key,
              canPreventDefault: true,
            });

            if (!isFocused && !event.defaultPrevented) {
              navigation.navigate(route.name, route.params);
            }
          };

          return (
            <TouchableOpacity
              accessibilityRole="button"
              style={styles.ctnItemTabBottom}
              accessibilityState={isFocused ? {selected: true} : {}}
              key={index}
              onPress={onPress}>
              {getIcon()}
              <TextCM
                style={isFocused ? styles.txtLabelSelect : styles.txtLabel}>
                {label}
              </TextCM>
            </TouchableOpacity>
          );
        })}
      </View>
    </SafeAreaView>
  );
};
const BottomTabNavigationERequest = () => {
  const {t} = useTranslation();
  const dispatch = useAppDispatch();
  const [totalRecoders, setTotalRecoders] = useState(0);
  const NotificationService = useNotificationServices();

  useEffect(() => {
    getNUmberOfNotiUnread();
  }, []);

  const getNUmberOfNotiUnread = async () => {
    const data = await NotificationService.getNumberOfNotiUnread();
    if (data) {
      dispatch(commonActions.setTotalNumberNotiUnread(data));
      setTotalRecoders(data);
    }
  };
  return (
    <Tabs.Navigator
      tabBar={props => (
        <MyTabBar {...props} t={t} totalRecoders={totalRecoders} />
      )}
      screenOptions={{headerShown: false}}>
      <Tabs.Screen name={TabName.Home} component={Home} />
      <Tabs.Screen name={TabName.MyTicket} component={MyTicket} />
      <Tabs.Screen name={TabName.FollowTicket} component={MyTicketFollow} />
      <Tabs.Screen name={TabName.Notification} component={NotificationScreen} />
    </Tabs.Navigator>
  );
};

export default BottomTabNavigationERequest;
